import React from "react";
import Input from "./Input";

function Login(){
    return(
        <form className="form">

                <Input
                type="text" 
                placeholder="username" /><br/>

                <Input
                type="password"
                placeholder="Password" /><br/>
              
                <button type ="submit" id="c">Login</button>
                
            </form>
    );

}


export default Login;